import streamSaver from 'streamsaver';

const CustomDownload=(fileName, fileID, notify, loadFilesNFolders)=>{
    let fileStream=null;

    let downloadErrorCheck = response =>{
      if(!response.ok){
        loadFilesNFolders();
        throw Error('  .');
      }
      
      return response;
    }

    let data={};
    let idSplit=fileID.split(' ');
    for(let id in idSplit){
      data['file' + String(Number(id)+1)] = idSplit[id];
    }

    fetch(`${window.location.origin}/api/download`, { //axios stream     fetch 
      method: "POST",
      headers: {
        'Content-Type' : 'application/json',
      },
      body : JSON.stringify(data),
      credentials: 'include'

    })
    .then(downloadErrorCheck)
    .then(content=>{
      if(idSplit.length>1){ //   ,    downloadFiles.zip 
        fileStream=streamSaver.createWriteStream('downloadFiles.zip');
      }

      else{
        fileStream=streamSaver.createWriteStream(fileName); // filename_here    . 
                                                                   //          
                                                                   //    .
      }

      const readableStream=content.body;
      if(window.WritableStream && readableStream.pipeTo){
        return readableStream.pipeTo(fileStream);
      }
    
      const writer=fileStream.getWriter()
      const reader=readableStream.getReader()
      const pump = () => reader.read()
      .then(res => res.done ? writer.close() : writer.write(res.value).then(pump))
    
      pump();
    }).catch(e=>notify(e))
    
}

export default CustomDownload;
